.hero {
  width: 100%;

  &__content {
    display: flex;
    flex-direction: column;

    background: url('/img/chocolate-covered.jpg') no-repeat center center;
    background-color: #b86c46;
    background-size: cover;
    min-height: 600px;
    border-radius: 1.875rem;
    padding: 2.5rem;
  }

  &__title {
    max-width: 484px;
    color: var(--neutral-100);
    font-size: var(--fs-28);
    font-weight: 500;
    line-height: 2.125rem;
    letter-spacing: -0.03em;
    text-align: left;
    margin-top: auto;
    margin-bottom: 32px;

  }

  &__content-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 32px 50px;
  }
}

.btn-wrap {
  display: flex;
  gap: 14px;
}

.scroll {
  display: flex;
  align-items: center;
  gap: 14px;

  &__text {
    color: var(--neutral-100);
    font-size: var(--fs-14);
    font-weight: 500;
    line-height: 1.125rem;
    letter-spacing: -0.03em;
    text-align: left;
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 38px;
    height: 38px;
    background-color: var(--neutral-100);
    border-radius: 10em;
    cursor: pointer;
    border: 0;
    fill: var(--accent-color);
    transition: background-color var(--trs-35), fill var(--trs-35), box-shadow var(--trs-35);

    &:focus {
      outline: none;
    }

    @include hover {
      background-color: var(--accent-color);
      fill: var(--neutral-100);
    }

    &.focus-visible {
      box-shadow: 0 0 0 3px var(--focus-visible);
    }
  }
}

/* ------------------- */
/* media               */
/* ------------------- */

@media screen and (max-width: 475px) {
  .hero__title {
    font-size: 20px;
  }

  .hero__content {
    padding: 1.25rem;
  }

  .btn--hero {
    padding: 0.75rem 1.75rem;
    font-size: 14px;
  }
}

@media screen and (max-width: 374px) {
  .btn-wrap {
    display: flex;
    flex-wrap: wrap;

  }
}
