.header {
  width: 100%;
}

.logo {
  display: flex;
  width: clamp(10.56rem, calc(4.82rem + 11.96vw), 13.31rem);

  &--link {
    flex-shrink: 0;
    margin-right: auto;
    padding-left: 5px;
    transition: var(--trs-35);

    &:focus {
      outline: none;
    }

    &.focus-visible {
      box-shadow: 0 0 0 3px var(--focus-visible);
      border-radius: 10em;
      padding: 5px;
    }
  }
}

.menu {
  display: flex;
  align-items: center;
}

.nav {
  margin-right: 100px;

  &__list {
    display: flex;
    align-items: center;
    list-style: none;
  }

  &__item:not(:last-child) {
    margin-right: 24px;
  }

  &__link {
    color: var(--primary-color);
    font-size: var(--fs-14);
    font-weight: 500;
    line-height: 1rem;
    letter-spacing: -0.02em;
    text-align: left;
    transition: color var(--trs-35);

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &.focus-visible {
      color: var(--accent-color);
    }
  }
}

.networks {
  display: flex;
  gap: 19px;
}

.instagram,
.twitter {
  display: flex;
  transition: fill var(--trs-35);
  outline: none;

  @include hover {
    fill: var(--accent-color);
    transition: fill var(--trs-35);
  }

  &.focus-visible {
    fill: var(--accent-color);
  }
}

.layer {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.8);
  top: 0;
  right: 0;
  z-index: 10;
  transition: var(--trs-35);
}

/* ------------------- */
/* media               */
/* ------------------- */

@media screen and (max-width: 1024px) {
  .menu {
    position: absolute;
    top: 0;
    left: -290px;
    display: inline-block;
    background-color: var(--primary-color);
    width: 100%;
    max-width: 290px;
    padding: 0 50px;
    height: 100vh;
    z-index: 100;
    transition: left var(--trs-35);
  }

  .nav {
    &--header {
      margin: 0;
    }
  }

  .nav__list {
    &--header {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      line-height: 3.125rem;
      padding: 0;
      margin-top: 156px;
    }
  }

  .nav__link {
    &--header {
      color: var(--neutral-100);
    }
  }

  .networks {
    &--header {
      margin-top: 50px;
    }
  }

  .twitter,
  .instagram {
    &--color {
      fill: var(--neutral-100);

      @include hover {
        fill: var(--accent-color);
      }
    }
  }

  .active {
    left: 0px;
    overflow-y: visible;
    transition: left var(--trs-35);
  }

  .burger {
    display: block;
    margin-left: 18px;
  }

  .close {
    visibility: visible;
    opacity: 1;
    display: block;
    transition: var(--trs-35);
  }
}
