.video {

  &__title {
    color: var(--primary-color);
    font-size: var(--fs-24);
    font-weight: 600;
    line-height: 1.75rem;
    text-transform: uppercase;
    letter-spacing: -0.03em;
    text-align: center;
    margin: 0;
    padding: 0;
    margin-bottom: 50px;

    &--color {
      color: var(--accent-color);
    }
  }

  &__player {
    position: relative;
    grid-column: 6 span;
    align-self: center;
    height: 100%;
    border-radius: 0.9375rem;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
  }

  &__poster {
    width: 100%;
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center center;
    border-radius: 0.9375rem;
  }

  &__content {
    grid-column: 6 span;
    height: 100%;
    padding: 32px;
    align-self: center;
    background-color: var(--neutral-100);
    border-radius: 15px;
    transition: padding var(--trs-35);
  }

  &__content-title {
    color: var(--primary-color);
    font-size: var(--fs-18);
    font-weight: 600;
    line-height: 1.5rem;
    letter-spacing: 0em;
    text-align: left;
    margin: 0;
    padding: 0;
    margin-bottom: 28px;
  }

  &__list {
    margin: 0;
    padding: 0;

    &--p {
      padding-left: 15px;
    }
  }

  &__item {
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__item p {
    color: var(--primary-color);
    font-size: var(--fs-14);
    font-weight: 400;
    line-height: 1.125rem;
    letter-spacing: -0.02em;
    text-align: left;
    margin: 0;
    padding: 0;
  }
}

.icon-play {
  transition: fill var(--trs-35);

  @include hover {
    fill: var(--accent-color);
  }
}

.btn-icon-play {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
  fill: var(--neutral-100);
  cursor: pointer;
  display: flex;
  background-color: transparent;
  border: 0;
  padding: 0;
  margin: 0;
  transition: fill var(--trs-35);

  &:focus {
    outline: none;
  }

  &.focus-visible {
    fill: var(--accent-color);
    border-radius: 50em;
  }
}

/* ------------------- */
/* media               */
/* ------------------- */

@media screen and (max-width: 768px) {

  .video__player,
  .video__content {
    grid-column: 12 span;
  }
}

@media screen and (max-width: 475px) {
  .video__content {
    padding: 20px 14px;
    transition: padding var(--trs-35);
  }
}
