:root {
  --ff-primary: "Montserrat", sans-serif;

  --content-width: 71rem; // 1136px;
  --container-offset: 0.9375rem; // 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  /* ------------------- */
  /* color theme         */
  /* ------------------- */
  --neutral-100: hsl(0, 0%, 100%);
  --neutral-200: hsla(0, 0%, 100%, 0.7);
  --neutral-300: hsla(0, 0%, 100%, 0.502);
  --neutral-900: hsl(0, 0%, 7%);
  --primary-color: hsl(273, 18%, 12%);
  --primary-color-dark: hsl(274, 18%, 8%);
  --accent-color: hsl(31, 98%, 56%);
  --accent-color-light: hsl(36, 82%, 93%);
  --active-color: hsla(0, 0%, 0%, 0);
  --focus-visible: hsl(221, 51%, 86%);
  --placeholder-color: hsl(0, 0%, 80%);
  /* ------------------- */
  /* font-size           */
  /* ------------------- */
  --fs-12: 0.75rem; // 12px;
  --fs-14: 0.875rem; // 14px;
  --fs-18: 1.125rem; // 18px;
  --fs-24: 1.5rem; // 24px;
  --fs-28: 1.75rem; // 28px;
  /* ------------------- */
  /* transition          */
  /* ------------------- */
  --trs-25: .25s ease-in-out;
  --trs-35: .35s ease-in-out;
  --trs-45: .45s ease-in-out;
  /* ------------------- */
  /* margin              */
  /* ------------------- */
  --mrg-50: 3.125rem; // 50px;
  --mrg-72: 4.5rem; // 72px;
  --mrg-128: 8rem; // 128px;
  --mrg-150: 9.375rem; // 150px;
  --mrg-200: 12.5rem; // 200px;
  /* ------------------- */
  /* gap                 */
  /* ------------------- */
  --gap: 1.25rem; // 20px;
}



