.benefits {
  width: 100%;
  padding: 50px 0;
  background-color: var(--primary-color);

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);

    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__item {
    padding: 0 32px;
  }

  &__item:not(:last-child) {
    position: relative;

    &:after {
      content: '';
      width: 1px;
      height: 50%;
      background-color: var(--neutral-300);

      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 62px;
    height: 62px;
    border-radius: 10em;
    border: 1px solid var(--neutral-300);
    margin-bottom: 22px;
  }

  &__text {
    display: flex;
    flex-direction: column;
  }

  &__title {
    color: var(--neutral-100);
    font-size: var(--fs-18);
    font-weight: 600;
    line-height: 1.3rem;
    letter-spacing: 0em;
    text-align: left;
    margin: 0;
    padding: 0;
    margin-bottom: 18px;
  }

  &__desc {
    color: var(--neutral-100);
    font-size: var(--fs-14);
    font-weight: 400;
    line-height: 1.2rem;
    letter-spacing: -0.02em;
    text-align: left;
    margin: 0;
    padding: 0;
  }
}

/* ------------------- */
/* media               */
/* ------------------- */

@media screen and (max-width: 768px) {
  .benefits__list {
    grid-template-columns: repeat(1, 1fr);
    gap: 30px 0;
  }

  .benefits__item:not(:last-child):after {
    display: none;
  }
}
