.products {
  padding-bottom: 3.125rem;

  &__title {
    color: var(--neutral-900);
    font-size: var(--fs-24);
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1.75rem;
    letter-spacing: -0.03em;
    text-align: center;
    margin: 0;
    padding: 0;
    margin-bottom: 3.125rem;

    &--color {
      color: var(--accent-color);
    }
  }
}

.card {
  // padding: 2rem 0px;
  background-color: var(--neutral-100);
  border: 1px solid hsla(0, 0%, 7%, 0.1);
  border-radius: 15px;
  text-align: center;
  transition: var(--trs-35);

  @include hover {
    border: 1px solid var(--accent-color);
  }

  &__img {
    width: 100%;
    display: block;
    margin-top: 2rem;
    margin-inline: auto;
  }

  &__text {
    padding-top: 0.625rem;
  }

  &__title {
    color: var(--neutral-900);
    font-size: var(--fs-18);
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1.5rem;
    letter-spacing: -0.02em;
    text-align: center;
    margin: 0;
    padding: 0;
    margin-bottom: 5px;
  }

  &__type {
    display: block;
    color: var(--neutral-900);
    font-family: var(--ff-primary);
    font-size: var(--fs-12);
    font-weight: 400;
    line-height: 0.875rem;
    letter-spacing: -0.02em;
    text-align: center;
    margin-bottom: 1.125rem;
  }

  &__price {
    display: inline-block;
    padding: 0.5rem 1.25rem;
    border-radius: 10em;
    border: 1px solid hsla(0, 0%, 7%, 0.1);
    margin-bottom: 2rem;
  }

  &__price-new {
    color: var(--neutral-900);
    font-family: var(--ff-primary);
    font-size: var(--fs-14);
    font-weight: 600;
    line-height: 1.0625rem;
    letter-spacing: 0em;
    text-align: center;
  }
}

.card-wrapper {
  display: block;
  width: 100%;
  height: 100%;

  &:focus {
    outline: none;
  }

  &.focus-visible {
    box-shadow: 0 0 0 3px var(--focus-visible);
    border-radius: 15px;
  }
}

.productsSwiper {
  padding: 3px;
}

.swiper-horizontal>.swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: -3.125rem;
}

.swiper-pagination-bullet-active {
  opacity: var(--swiper-pagination-bullet-opacity, 1);
  background: var(--accent-color);
  width: 28px !important;
  height: 8px;
  border-radius: 10px;
}

.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
}

.wrapper-pagination {
  position: relative;
}

/* ------------------- */
/* media               */
/* ------------------- */

@media screen and (max-width: 1023px) {

  .container {
    &--padding-left {
      padding-left: 15px;
      padding-right: 0;
    }
  }
}

@media screen and (max-width: 574px) {

  .container {
    &--padding-left {
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}

@media screen and (max-width: 474px) {

  .container {
    &--padding-left {
      padding-left: 15px;
      padding-right: 0;
    }
  }
}

@media screen and (max-width: 374px) {

  .container {
    &--padding-left {
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}
