.btn {
  // Reset
  appearance: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-decoration: none !important;
  line-height: 1;
  white-space: nowrap;
  margin: 0;
  cursor: pointer;
  vertical-align: baseline;
  position: relative;
  user-select: none;
  z-index: 1;

  // Text
  font-family: var(--ff-primary);
  font-size: 1em; // 16px;
  font-weight: 400;
  text-align: center;
  text-transform: none;

  // Appearance
  gap: 0.5em; // 8px
  border: 0.125em solid transparent; // 2px;
  border-radius: 10em; // 160px
  padding: 0.875em 2.5em; // 14px 40px;
  color: var(--neutral-100);
  background-color: var(--accent-color);
  transition: var(--trs-35);

  // Animations
  &:focus {
    outline: none;
  }

  &:hover,
  &:active {
    color: var(--accent-color);
    // box-shadow: inset 0 0 0 100px rgba(0, 0, 0, .2);
    background-color: var(--accent-color-light);
  }

  // .btn.secondary
  &.secondary {
    color: var(--neutral-900);
    background-color: hsl(0, 0%, 80%);

    &:hover,
    &:active {
      color: var(--neutral-100);
      background-color: hsl(0, 0%, 20%);
    }
  }

  // .btn.outline
  &.outline {
    color: var(--neutral-100);
    border-color: var(--neutral-100);
    background-color: transparent;

    &:hover,
    &:active {
      color: var(--accent-color);
      border-color: var(--accent-color);
    }
  }

  &.outline-accent {
    color: var(--neutral-900);
    border-color: var(--accent-color);
    background-color: transparent;

    &:hover,
    &:active {
      color: var(--neutral-100);
      border-color: var(--accent-color);
      background-color: var(--accent-color);
    }
  }

  // .btn.text
  &.text {
    color: var(--primary-color);
    background-color: transparent;

    &:hover,
    &:active {
      border-color: var(--primary-color);
    }
  }

  // sizes button
  &.xs {
    padding: 0.375rem 1rem; // 6px 16px;
    font-size: 0.625rem; // 10px;
  }

  &.sm {
    padding: 0.625em 1.25em; // 10px 20px;
    font-size: 0.875em; // 14px;
  }

  &.lg {
    padding: 1em 2em; // 16px 32px;
    font-size: 1.25em; // 20px;
  }

  &.wide {
    display: flex;
    width: 100%;

    >*:not(:first-child) {
      margin-left: auto;
    }
  }
}

.btn.focus-visible {
  box-shadow: 0 0 0 3px var(--focus-visible);
}
