.ingredients {
  width: 100%;

  &__title {
    color: var(--primary-color);
    font-size: var(--fs-24);
    font-weight: 600;
    line-height: 1.75rem;
    letter-spacing: -0.03em;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    padding: 0;
    margin-bottom: 1.5rem;
    max-width: 411px;
    margin-inline: auto;

    &--color {
      color: var(--accent-color);
    }
  }

  &__desc {
    color: var(--primary-color);
    font-size: var(--fs-14);
    font-weight: 400;
    line-height: 1.125rem;
    letter-spacing: -0.02em;
    text-align: center;
    margin: 0;
    padding: 0;
    margin-bottom: 3.125rem;
  }

  &__item {
    overflow: hidden;
    position: relative;
    grid-column: 3 span;
    // margin: 2px;
    // padding: 4px;
    border-radius: 0.9375rem;

    &:hover .dropmenu {
      opacity: 1;
      visibility: visible;
      transform: translateY(0px);
      transition: var(--trs-45);
      z-index: 5;
    }
  }

  &__link {
    overflow: hidden;
    position: relative;

    border: 1px solid var(--accent-color);
    padding: 1.75rem;
    display: flex;
    flex-direction: column;
    height: 100%;
    gap: 1.875rem;
    border-radius: 0.9375rem;
    transition: box-shadow var(--trs-35);

    &--accent {
      background-color: var(--accent-color);
    }

    &--accent h4 {
      color: var(--neutral-100);
    }

    &--dark {
      background-color: var(--neutral-900);
      border: 1px solid var(--neutral-900);
    }

    &--dark h4 {
      color: var(--neutral-100);
    }

    &:focus {
      outline: none;
    }

    &.focus-visible {
      box-shadow: 0 0 0 3px var(--focus-visible);
    }
  }

  &__link-title {
    color: var(--primary-color);
    font-size: var(--fs-14);
    font-weight: 600;
    line-height: 1.125rem;
    letter-spacing: -0.02em;
    text-align: left;
    text-transform: uppercase;
    margin: 0;
    padding: 0;
    margin-top: auto;
  }

  &__img {
    display: block;
    margin-inline: auto;
  }
}

.dropmenu {
  display: flex;
  flex-direction: column;
  justify-content: center;

  padding: 1.25rem;
  border-radius: 0.9375rem;
  height: 100%;
  text-align: left;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--neutral-100);

  transform: translateY(270px);
  transition: var(--trs-45);
  opacity: 0;
  visibility: hidden;

  &__title {
    color: var(--neutral-900);
    font-family: var(--ff-primary);
    font-size: var(--fs-18);
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1;
    letter-spacing: -0.03em;
    text-align: left;
    margin: 0;
    padding: 0;
    margin-bottom: 0.5rem;
  }

  &__desc {
    color: var(--neutral-900);
    font-family: var(--ff-primary);
    font-size: var(--fs-14);
    font-weight: 400;
    text-transform: initial;
    line-height: 1.25em;
    letter-spacing: -0.02em;
    text-align: left;
    margin: 0;
    padding: 0;
    margin-top: 0.875rem;

    display: -webkit-box;
    -webkit-line-clamp: 9;
    -webkit-box-orient: vertical;
    overflow: hidden;

    &--seven-lines {
      display: -webkit-box;
      -webkit-line-clamp: 8;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}

.btn-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

/* ------------------- */
/* media               */
/* ------------------- */

@media screen and (max-width: 768px) {
  .ingredients__item {
    grid-column: 6 span;
  }
}

@media screen and (max-width: 475px) {

  .ingredients__item {
    grid-column: 12 span;
  }
}
