.subcribe {
  position: relative;
  background: url('/img/subcribe.jpg') no-repeat center center;
  background-color: #fff;
  background-size: cover;
  min-height: 370px;
  text-align: center;
  @include flex-f-cln;
  justify-content: center;

  &__title {
    color: var(--neutral-100);
    font-size: var(--fs-24);
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1.75rem;
    letter-spacing: -0.03em;
    text-align: center;
    margin: 0;
    padding: 0;
    margin-inline: auto;
    margin-bottom: 24px;
    max-width: 378px;
  }

  &__desc {
    color: var(--neutral-200);
    font-size: var(--fs-14);
    font-weight: 400;
    line-height: 1.125rem;
    letter-spacing: -0.02em;
    text-align: center;
    margin: 0;
    margin-bottom: 24px;
    max-width: 467px;
  }
}

.graph-modal {
  left: -5px;

  &__container {
    max-width: 538px;
    padding: 0;
    border-radius: 15px;
    overflow: hidden;
  }

  &__info {
    padding: 40px;
  }

  &__title {
    color: var(--primary-color);
    font-size: var(--fs-24);
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1.75rem;
    letter-spacing: -0.03em;
    text-align: left;
    max-width: 298px;
    margin: 0;
    margin-bottom: 28px;

    &--color {
      color: var(--accent-color);
    }
  }
}

.graph-modal__close {
  position: absolute;
  right: 10px;
  top: 10px;
  border: none;
  padding: 0;
  stroke: var(--neutral-100);
  width: initial;
  height: initial;
  background-color: transparent;
  background-image: none;
  cursor: pointer;
  transition: var(--trs-35);

  @include hover {
    stroke: var(--accent-color);
  }
}

.email {

  &__wrap {
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: space-between;
  }

  &__input {
    height: 53px;
    display: block;
    background-color: var(--neutral-100);
    width: 100%;
    border-radius: 10em;
    padding-left: 24px;
    border: 1.5px solid hsla(0, 0%, 7%, 0.05)
  }

  &__btn {
    padding: 1.125em 2em;
  }
}

.email__input::placeholder {
  color: hsla(0, 0%, 25%, 0.3);
  font-size: var(--fs-18);
  font-weight: 400;
  line-height: 1.375rem;
  letter-spacing: -0.02em;
  text-align: left;
}

