input.email__input {
  outline: none;
}

/* ------------------- */
/* media               */
/* ------------------- */

@media screen and (max-width: 475px) {
  .section-top {
    margin-top: var(--mrg-72);
  }

  .email__btn {
    padding: 14px 22px;
  }

  .graph-modal__info {
    padding: 40px 24px;
  }

  .graph-modal__title {
    font-size: 20px;
  }

  .email__input {
    height: 50px;
    border: 0;
  }

  .email__wrap {
    gap: 0px;
    border: 1.5px solid hsla(0, 0%, 7%, 0.05);
    border-radius: 10em;
  }

  input.email__input::placeholder {
    font-size: 14px;
  }

  .graph-modal__title {
    &--color {
      display: block;
    }
  }
}
