.sellers {

  &__title {
    color: var(--neutral-900);
    font-size: var(--fs-24);
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1.75rem;
    letter-spacing: -0.03em;
    text-align: center;
    margin: 0;
    padding: 0;
    margin-bottom: 50px;

    &--color {
      color: var(--accent-color);
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    grid-column: 4 span;

    &--shadow {
      border-radius: 15px;
      box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 5px 0px;
    }

    &--bottom {
      grid-column: 12 span;
      @include flex-d-sb;
      align-items: flex-start;
    }
  }

  &__item p {
    color: var(--neutral-900);
    font-size: var(--fs-14);
    font-weight: 400;
    line-height: 1.125rem;
    letter-spacing: -0.02em;
    text-align: left;
    max-width: 407px;
    margin: 0;
  }

  &__img {
    width: 100%;
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center center;
    border-radius: 15px;
  }
}

/* ------------------- */
/* media               */
/* ------------------- */

@media screen and (max-width: 768px) {
  .sellers__item {
    grid-column: 6 span;
  }

  .sellers__item:nth-child(4) {
    grid-column: 12 span;
    gap: 0 50px;
  }

  .sellers__item:nth-child(3) {
    display: none;
  }
}

@media screen and (max-width: 475px) {
  .sellers__item {
    grid-column: 12 span;
  }

  .sellers__item {
    &--bottom {
      display: flex;
      flex-wrap: wrap;
    }
  }

  .sellers__item p {
    margin-bottom: 20px;
    max-width: 100%;
    text-align: center;
  }

  .sellers__btn {
    margin-inline: auto;
  }
}
