.reviews {

  &__title {
    color: var(--primary-color);
    font-size: var(--fs-24);
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1.75rem;
    letter-spacing: -0.03em;
    text-align: center;
    margin: 0;
    padding: 0;
    margin-bottom: 50px;

    &--color {
      color: var(--accent-color);
    }
  }

  &__btn {
    display: block;
    margin-top: 50px;
    margin-inline: auto;

    border: 1px solid var(--accent-color);
  }
}

.pagination {
  margin-top: 32px;
  display: flex;
  justify-content: center;
}

.swiper {
  padding-top: 40px;
}

.reviews-card {
  position: relative;
  background-color: var(--neutral-100);
  border: 1px solid hsla(0, 0%, 7%, 0.1);
  border-radius: 0.9375rem;
  height: 100%;
  text-align: center;
  padding: 56px 24px 24px 24px;

  &__avatar {
    display: flex;
    position: absolute;
    top: 0%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    border-radius: 10em;
    border: 2px solid hsla(0, 0%, 87%, 0.9);
  }

  &__title {
    color: var(--primary-color);
    font-size: var(--fs-18);
    font-weight: 600;
    line-height: 1.5rem;
    letter-spacing: 0em;
    text-align: center;
    margin: 0;
    margin-bottom: 24px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  &__desc {
    color: var(--primary-color);
    font-size: var(--fs-14);
    font-weight: 400;
    line-height: 1.125rem;
    letter-spacing: -0.02em;
    text-align: center;
    margin: 0;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
