.footer {
  padding: 50px 0;
  background-color: var(--primary-color);

  &__top {
    display: flex;
    margin-bottom: 62px;
  }

  &__title {
    color: var(--neutral-100);
    font-size: var(--fs-28);
    font-weight: 500;
    line-height: 2.125rem;
    letter-spacing: -0.02em;
    text-align: left;
    margin: 0;
    max-width: 300px;

    &--color {
      color: var(--accent-color);
    }
  }

  &__bottom {
    border-top: 1px solid hsla(0, 0%, 100%, 0.1);
    padding-top: 24px;
  }
}

.contact {
  margin-left: auto;
  margin-right: 65px;

  &__title {
    color: var(--neutral-100);
    font-size: var(--fs-14);
    font-weight: 500;
    line-height: 1.125rem;
    letter-spacing: 0em;
    text-align: left;
    margin: 0;
    margin-bottom: 14px;
  }

  &__tel {
    display: block;
    color: var(--neutral-100);
    font-size: var(--fs-14);
    font-weight: 400;
    font-style: normal;
    line-height: 1.125rem;
    letter-spacing: -0.02em;
    text-align: left;
    margin: 0;
    margin-bottom: 5px;
  }

  &__mail {
    display: block;
    color: var(--neutral-100);
    font-size: var(--fs-14);
    font-weight: 400;
    font-style: normal;
    line-height: 1.125rem;
    letter-spacing: -0.02em;
    text-align: left;
    margin: 0;
  }

  &__tel,
  &__mail {
    transition: color var(--trs-35);

    &:focus {
      outline: none;
    }

    &:hover {
      color: var(--accent-color);
    }

    &.focus-visible,
    &.focus-visible {
      color: var(--accent-color);
    }
  }
}

.networks {
  &__link {
    &--color {
      fill: var(--neutral-100);
      display: flex;
      transition: fill var(--trs-35);

      &:focus {
        outline: none;
      }

      @include hover {
        fill: var(--accent-color);
      }

      &.focus-visible {
        fill: var(--accent-color);
      }
    }
  }
}

.nav {
  &__link {
    &--color {
      color: var(--neutral-100);
    }
  }
}

/* ------------------- */
/* media               */
/* ------------------- */

@media screen and (max-width: 768px) {
  .footer__title {
    font-size: 24px;
  }

  .footer__top {
    display: grid;
    grid-template-columns: 1fr;
    gap: 40px 0;
  }

  .contact {
    margin-left: 0;
    margin-right: 65px;
  }

  .nav__list {

    &--footer {
      display: flex;
      gap: 10px 0;
      flex-wrap: wrap;
    }
  }

  .nav {
    margin-right: 0px;

  }
}

@media screen and (max-width: 475px) {

  .footer {
    padding: 32px 0;
    transition: var(--trs-35);
  }

  .footer__top {
    margin-bottom: 32px;
    transition: var(--trs-35);
  }

  .nav__list {
    &--footer {
      margin: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 0;
      transition: var(--trs-35);
    }
  }
}
